}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --desk: 1200px;
  --tab: 768px;
  --mob: 480px;
  --font-primary: "Montserrat", sans-serif;
  --font-title: "Seymour One", sans-serif;
  --color-text-light: #fcfcfc;
  --color-text-dark: #131313;
  --color-bg-primary: #84a178;
  --color-btn-default: #f8d24e;
  --color-btn-hover: #0a8817;
  --color-btn-disabled: #ababab;
  --color-bg-product-green: #aec6a5;
  --color-bg-product-yellow: #efd478;
  --color-bg-product-brown: #d6936d;
  --color-bg-input: rgba(248, 210, 78, .1);
  --cubic: 250ms cubic-bezier(.4, 0, .2, 1);
  --color-modal-bg-green: #9cbe8f;
  --color-modal-bg-picture-shadow: rgba(156, 190, 143, .7);
  --color-modal-bg-shadow-green: rgba(10, 136, 23, .25);
  --color-coffee-shadow: rgba(248, 210, 78, .6);
  --color-coffee-second-shadow: rgba(248, 210, 78, .9);
  --color-shakes: #d6936d;
  --color-shake-shadow: rgba(0, 0, 0, .25);
